body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative; /* Ensure relative positioning for absolute elements */
}

.create-container {
  display: flex;
  height: 100vh;
  flex-direction: row; /* Set flex direction to row */
}

.nav-bar {
  width: 80px;
  background-color: rgb(13, 148, 136);
  position: relative;
  height: 100vh; /* Set height to 100% of viewport height */
}

.nav-bar > div {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.first-nav-div {
  position: absolute;
  top: 5px;
  width: 48px;
  height: 48px;
  background-image: url('../src/assets/images/Group\ 24.png');
  background-repeat: no-repeat;
  background-size: contain;
}
.last-nav-div {
  position: absolute;
  bottom: 5px;
  width: 48px;
  height: 48px;
  border-radius: 48px;
  background-image: url('../src/assets/images/Rectangle\ 2626.png');
  background-repeat: no-repeat;
  background-size: contain;
}
.centered-div {
  position: absolute;
  top: 25%;
  bottom: 25%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly !important;
  align-items: center;
}
.parent-kanban {
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center; /* Horizontal alignment */
  align-items: center; /* Vertical alignment */
}
.navimage-kanban {
  width: 22px;
  height: 22px;
  background-image: url('../src/assets/images/Group\ 144.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.parent-scheduler {
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center; /* Horizontal alignment */
  align-items: center; /* Vertical alignment */
}
.navimage-scheduler {
  width: 22px;
  height: 22px;
  background-image: url('../src/assets/images/Group\ 142.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.parent-gantt {
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center; /* Horizontal alignment */
  align-items: center; /* Vertical alignment */
}
.navimage-gantt {
  width: 22px;
  height: 22px;
  background-image: url('../src/assets/images/Group\ 143.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.parent-grid {
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center; /* Horizontal alignment */
  align-items: center; /* Vertical alignment */
}
.navimage-grid {
  width: 22px;
  height: 22px;
  background-image: url('../src/assets/images/Group\ 123.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; 
}
.main-content {
  flex: 1;
  padding: 20px;
  box-sizing: border-box;
  position: relative; /* Ensure position is relative for child elements */
}

.title-container {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between; /* Align items in the title container */
  align-items: center; /* Center items vertically */
}

.title {
  font-size: 25px;
  margin: 0; /* Remove the bottom margin */
  margin-right: auto;
  font-family: Inter;
  font-weight: 600;
}

.button {
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 110px;
  height: 30px;
  background-image: url("../src/assets/images/button.png");
}

.circle {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-image: url("../src/assets/images/Rectangle\ 2626.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size:contain;
}

.extra-circle {
  width: 36px;
  height: 36px;
  background-image: url("../src/assets/images/Group\ 24M.png");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 10px;
}
.card-container {
  display: flex;
  flex-wrap: wrap;
}
.inner-cadr {
  width: calc(25.65% - 10px);
  border: 1.5px solid #f0f0f0;
  padding: 15px;
  padding-left: 0;
  margin-right: 2px;
  margin-bottom: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
}
.mainimagetodo,
.mainimageprogress,
.mainimagetest,
.mainimagedone {
  width: 40%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.mainimagetodo {
  background-image: url('../src/assets/images/to\ do\ bg.png');
}
.mainimageprogress {
  background-image: url('../src/assets/images/Inprogress\ bg.png');
}
.mainimagetest {
  background-image: url('../src/assets/images/testing\ bg.png');
}
.mainimagedone {
  background-image: url('../src/assets/images/done\ bg.png');
}
.detailcontainertodo {
  width: 60%;
  display: flex;
  flex-direction: column;
}
.texttodo {
  width: 100%;
  font-family: Inter;
  font-size: 20px;
  font-weight: 600;
  line-height: 15px;
  letter-spacing: 0em;
  text-align: left;
  margin-bottom: 28px;
}
.counttodo {
  width: 100%;
  font-family: Inter;
  font-size: 40px;
  font-weight: 600;
  line-height: 15px;
  letter-spacing: 0em;
  text-align: left;
}
.dropdown-container {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between; /* Align dropdowns to the left and right */
  align-items: center; /* Center items vertically */
}

.custom-dropdown:nth-child(2) {
  margin-left: auto; /* Push the second dropdown to the right */
}

.custom-dropdown {
  width: calc(33% - 10px); /* Set width of dropdowns */
  max-width: 200px; /* Set max-width for dropdowns */
}
#image-container {
  position: relative; 
  height: 30px;
}

#image-container img {
  position: absolute; /* Set position absolute for precise positioning */
}

#image-container img:nth-child(1) {
  left: 0; /* Position the first image at the left edge */
}

#image-container img:nth-child(2) {
  left: 34px; /* Position the second image with some overlap from the first */
}

#image-container img:nth-child(3) {
  left: 68px; /* Position the third image with some overlap from the second */
}

#image-container img:nth-child(4) {
  left: 102px; /* Position the fourth image with some overlap from the third */
}

#image-container img:nth-child(5) {
  left: 136px; /* Position the fifth image with some overlap from the fourth */
}
#timerangecompo {
  width: calc(90% - 10px);
  height: 30px;
}
.add-overflow {
  overflow-y: auto;
  height :64vh;
  max-height: 64vh; /* Set max-height to 70% of viewport height */
}
#component-render-grid {
  height: 78% !important;
}

#component-render-scheduler,#component-render-gantt,#component-render-grid {
display: none;
transition: opacity 0.5s ease-in-out;
}

#component-renderf.show1,
#component-render-scheduler.show1, #component-render-gantt.show1, #component-render-grid.show1{
display: block;
opacity: 1;
}
.show1-background {
  background-color: rgba(15, 118, 110, 1);
  border-radius:8px
}
#component-render-gantt {
  width: 90vw !important;
}

.parent-kanban:hover, .parent-scheduler:hover, .parent-gantt:hover, .parent-grid:hover{
  border: 1px solid white;
}
.circular-image:hover {
  box-shadow: 0 0 10px rgba(13, 148, 136, 1);
}
.circular-image {
  border-radius: 50%;
}
.selected-image {
  border: 2px solid rgba(13, 148, 136, 1)
}
#component-render-grid .e-gridcontent {
 border-bottom: 0.1px solid lightgrey;
}
/* Media query for mobile view */
@media only screen and (max-width: 700px) {
  .nav-bar {
      display: none; /* Hide the normal nav bar */
  }
  #component-renderf {
      width: 90vw !important;
   }
   #component-render-gantt {
      width: 90vw !important;
   }
   #component-render-grid {
      width: 90vw !important;
   }
  .mobile-nav-bar {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 50px; /* Set height as needed */
      background-color: rgb(13, 148, 136);
      display: flex;
      justify-content: space-around;
      align-items: center;
      z-index: 1000;
  }

  .create-container {
      position: relative;
  }

  .button {
      display: none; /* Hide the button in mobile view */
  }

  .circle {
      display: block; /* Show the circle in mobile view */
  }

  .extra-circle {
      display: block; /* Show the extra circle in mobile view */
  }

  .dropdown-container {
      flex-direction: column; /* Align dropdowns vertically in mobile view */
      justify-content: flex-start; /* Align dropdowns to the start in mobile view */
  }
  .e-input-group[aria-describedby="datasourceDropDown"] {
     width: 100% !important;
     margin-bottom: 5px;
  }
  #timerangecompo {
      width: 100% !important;
  }
  .e-input-group.e-date-range-wrapper {
      width: 100% !important;
  }
  .custom-dropdown {
      width: 100%; /* Set width to 100% in mobile view */
      max-width: none; /* Remove max-width in mobile view */
      margin-right: 0;
      margin-bottom: 10px; /* Add space between dropdowns */
  }

  /* New div for mobile view */
  .custom-div1 {
      position: fixed;
      bottom: 50px; /* Adjust distance from bottom */
      right: 20px; /* Adjust distance from right */
      z-index: 1000;
  }
  .custom-div1:before {
      content: url("../src/assets/images/Group\ 1893.png");
  }
  .title-container {
      position:sticky;
      top: 5px;
      z-index: 1;
  }
  .component-container1 {
      max-height: 90vh;
      overflow-y: auto;
      padding-bottom: 50px;
  }
  #component-render-scheduler .e-content-wrap tbody {
     height: 90vh;
  }
  #component-render-grid .e-gridcontent {
      width: 300% !important;
  }
  #component-render-grid #component-render-grid_toolbarItems {
      width: 300% !important;
  }
  #component-render-grid .e-gridheader {
      width: 300% !important;
  }
  .parent-kanban:hover, 
  .parent-scheduler:hover, 
  .parent-gantt:hover, 
  .parent-grid:hover {
      border: none; /* Remove the border */
  }
  .circular-image:hover {
      box-shadow: none; /* Remove the box shadow */
  }
  
}

