.bootstrap-dark .e-kanban .e-card-avatar,
.highcontrast .e-kanban .e-card-avatar {
  background: #4b5563;
  color: #fff;
}

@font-face {
  font-family: 'Kanban priority icons';
  src:
      url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype');
  font-weight: normal;
  font-style: normal;
}

[class^="sf-icon-"],
[class*=" sf-icon-"] {
  font-family: 'Kanban priority icons' !important;
  speak: none;
  font-size: 55px;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.e-location-container,
.e-all-day-time-zone-row,
.e-recurrenceeditor,
.e-description-row {
  display: none !important;
}